.cov {
  width: 100%;
  background-color: white;
  z-index: 100;
}

.cov .nav {
  z-index: 100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: white;
  position: fixed;
  width: 100%;
  height: 5.5vh;
}

.cov .nav .menu {
  display: none;
}

.cov .nav .logo {
  width: 20%;
}

.cov .nav .links ul {
  display: flex;
}

.cov .nav .links ul .link {
  margin-right: 25px;
  color: black;
  font-size: 17px;
  transition: all 0.2s ease-in-out;
  padding-bottom: 5px;
  border-bottom: 1px solid transparent;
}

.cov .nav .links ul .link:hover {
  transition: all 0.5s ease-in-out;
  border-bottom: 1px solid #cf2d2d;
}

.cov .nav .links ul .active {
  border-bottom: 1px solid #cf2d2d;
}

.cov .nav .links ul .last-link {
  margin-right: 60px;
}

@media only screen and (max-width: 768px) {
  .cov {
    width: 100%;
    overflow: hidden;
  }
  .cov .nav {
    padding: 0;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 10px;
    padding-left: 10px;
    overflow: hidden;
  }
  .cov .nav .links {
    display: none;
  }
  .cov .nav .logo {
    width: 40%;
    height: auto;
    margin-top: 10px;
  }
  .cov .nav .menu {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: white;
    display: block;
    text-align: right;
    margin-right: 25px;
    cursor: pointer;
  }
  .cov .nav .menu:active {
    opacity: 0;
    background-color: white;
  }
  .overlay-side {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.541);
    position: absolute;
    transition: all 0.5s ease-in-out;
    z-index: 150;
  }
  .sidebar {
    position: absolute;
    transition: all 0.5s ease-in-out;
    height: 100%;
    width: 0%;
    background-color: white;
    transform: translateX(0px);
    z-index: 200;
  }
  .sidebar .sidebar-bar {
    width: 100%;
    background-color: #ec1b23;
    margin-bottom: 10px;
  }
  .sidebar .sidebar-bar .inner {
    width: 93%;
    height: 8.4vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
  }
  .sidebar .sidebar-bar .inner .img {
    width: 60%;
  }
  .sidebar .sidebar-bar .inner span {
    width: 10%;
    cursor: pointer;
  }
  .sidebar .side-links {
    margin-top: 25px;
    width: 100%;
    height: 100%;
    background-color: white;
  }
  .sidebar .side-links ul {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    height: 100%;
  }
  .sidebar .side-links ul .link {
    padding-left: 20px;
    padding-bottom: 20px;
    padding-top: 20px;
    border-bottom: 1px solid #9c9b9b;
  }
  .sidebar h1 {
    color: white;
  }
}
